const PATTERNS = {
    Latitude : /^-?(0|[1-9]\d*)(\.\d+)?$/,
    Longitude : /^-?(0|[1-9]\d*)(\.\d+)?$/
}

export function validateCoordinates(lat,lon) {
    if (isNaN(lat) || isNaN(lon))
        throw new Error("–í–≤–µ–¥–∏—Ç–µ —á–∏—Å–ª–æ–≤—ã–µ –∑–Ω–∞—á–µ–Ω–∏—è");

    if (!(PATTERNS.Latitude.test(lat) && PATTERNS.Longitude.test(lon)))
        throw new Error("–í–≤–µ–¥–∏—Ç–µ –≤–∞–ª–∏–¥–Ω—ã–µ –¥–∞–Ω–Ω—ã–µ")

    if (lat < -90 || lat > 90)
        throw new Error("–®–∏—Ä–æ—Ç–∞ –¥–æ–ª–∂–Ω–∞ –±—ã—Ç—å –æ—Ç -90 –¥–æ 90");

    if (lon < -180 || lon > 180)
        throw new Error("–î–æ–ª–≥–æ—Ç–∞ –¥–æ–ª–∂–Ω–∞ –±—ã—Ç—å –æ—Ç -180 –¥–æ 180");
}